        </h3>
        <select v-if="PSs[CurrentPlace - 8].state == 0" v-model="PSWT">
          <option disabled value=""> </option>
          <option v-if="PSs[CurrentPlace - 8].normal == 1" value="normal">
            
          </option>
          <option v-if="PSs[CurrentPlace - 8].multi == 1" value="multi">
            
          </option>
          <option v-if="PSs[CurrentPlace - 8].plus == 1" value="plus">
            
          </option>
          <option v-if="PSs[CurrentPlace - 8].netflix == 1" value="netflix">
            
          </option>
        </select>
        <input
          v-if="PSs[CurrentPlace - 8].state == 0"
          placeholder="  "
          type="number"
          v-model="PSTT"
        />
        <button
          style="background-color: green"
          v-if="PSs[CurrentPlace - 8].state == 0"
          @click="PlayPS(CurrentPlace - 8)"
          type="button"
        >
          
        </button>
        <button
          style="background-color: red"
          v-else
          @click="StopPS(CurrentPlace - 8)"
          type="button"
        >
          
        </button>
      </form>
    </div>
  </div>
  <div class="no" v-else>
    <div> ,     </div>
  </div>
</template>

