<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      // Show Page
      RoomsShow: "",
      id: "",
      username: "",
      // Rooms
      Places: [
        { id: "Room1", Room: " 1", items: [], Total: 0 },
        { id: "Room2", Room: " 2", items: [], Total: 0 },
        { id: "Room3", Room: " 3", items: [], Total: 0 },
        { id: "Room4", Room: " 4", items: [], Total: 0 },
        { id: "Room5", Room: " 5", items: [], Total: 0 },
        { id: "Room6", Room: " 6", items: [], Total: 0 },
        { id: "Room7", Room: " 7", items: [], Total: 0 },
        { id: "Room8", Room: " 8", items: [], Total: 0 },
        { id: "PS1", Room: "PS1", items: [], Total: 0 },
        { id: "PS2", Room: "PS2", items: [], Total: 0 },
        { id: "PS3", Room: "PS3", items: [], Total: 0 },
        { id: "PS4", Room: "PS4", items: [], Total: 0 },
        { id: "PS5", Room: "PS5", items: [], Total: 0 },
        { id: "PS6", Room: "PS6", items: [], Total: 0 },
        { id: "PS7", Room: "PS7", items: [], Total: 0 },
        { id: "PS8", Room: "PS8", items: [], Total: 0 },
        { id: "Delivery", Room: "Delivery", items: [], Total: 0 },
        { id: "TakeAway", Room: "TakeAway", items: [], Total: 0 },
      ],
      Place: {},
      CurrentPlace: "",
      NewPlace: "",
      ButtonsShow: 1,
      Sectors: [],
      Products: [],
      FilteredProducts: [],
      Sector: "",
      Product: "",
      Quantity: "",
      Product2: "",
      Quantity2: "",
      //PSs
      PSs: [],
      PSWT: "",
      PSTT: 0,
      PSsShow: 0,
      PSW: ["", "", "", "", "", "", "", ""],
      PST: [0, 0, 0, 0, 0, 0, 0, 0],
      // time
      time: "",
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.username = this.getCookie("user_name");
    this.RoomsShow = this.getCookie("user_rooms");
    if (!this.getCookie("user_id")) {
      this.$router.push("/cafe-ps8/");
    }
    this.GetData();
    this.getTime();
    this.startInterval();
    this.$store.dispatch("globalFunction");
  },
  watch: {
    Sector: function () {
      this.FilteredProducts = this.Products.filter(
        (item) => item.id_sector == this.Sector
      );
    },
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    ShowPlace(P, i) {
      this.Place = P;
      if (i > 7 && i < 16) {
        this.PSsShow = 1;
      } else {
        this.PSsShow = 0;
      }
      this.ButtonsShow = 0;
      this.CurrentPlace = i;
    },
    BackPlaces() {
      this.ButtonsShow = 1;
    },
    async GetData() {
      try {
        const response = await axios.get(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            params: {
              timestamp: new Date().getTime(),
            },
          }
        );
        this.Sectors = response.data.Sectors;
        this.Products = response.data.Products;
        this.Places = JSON.parse(response.data.Places[0].orders);
        this.PSW = JSON.parse(response.data.Places[1].orders);
        this.PST = JSON.parse(response.data.Places[2].orders);
        this.PSs = response.data.PSs;
        this.$forceUpdate();
      } catch (error) {
        console.error("fail:", error);
      }
      this.Place = this.Places[this.CurrentPlace];
    },
    async AddItem() {
      const product = this.Products.filter(
        (Product) => Product.id == this.Product
      )[0];
      const price = Number(product.price).toFixed(2);
      const name = product.name;

      let foundItem = this.Places[this.CurrentPlace].items.find(
        (i) => i.Order == name
      );
      if (foundItem) {
        foundItem.Quantity += Math.abs(this.Quantity.toFixed(2));
      } else {
        this.Places[this.CurrentPlace].items.push({
          Order: name,
          Quantity: Math.abs(this.Quantity.toFixed(2)),
          Price: price,
        });
      }

      let total = 0;
      this.Places[this.CurrentPlace].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      this.Places[this.CurrentPlace].Total = total.toFixed(2);
      this.Product = "";
      this.Sector = "";
      this.Quantity = "";
      this.$forceUpdate();
      await this.UpdateItems();
      await this.GetData();
    },
    async AddItem2() {
      const product = this.Products.filter(
        (Product) => Product.id == this.Product2
      )[0];
      const price = Number(product.price).toFixed(2);
      const name = product.name;

      let foundItem = this.Places[this.CurrentPlace].items.find(
        (i) => i.Order == name
      );
      if (foundItem) {
        foundItem.Quantity += Math.abs(this.Quantity2.toFixed(2));
      } else {
        this.Places[this.CurrentPlace].items.push({
          Order: name,
          Quantity: Math.abs(this.Quantity2.toFixed(2)),
          Price: price,
        });
      }

      let total = 0;
      this.Places[this.CurrentPlace].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      this.Places[this.CurrentPlace].Total = total.toFixed(2);
      this.Product2 = "";
      this.Quantity2 = "";
      this.$forceUpdate();
      await this.UpdateItems();
      await this.GetData();
    },
    async RemoveItem(key) {
      const order = this.Places[this.CurrentPlace].items[key].Order;
      if (
        !(
          order == "normal" ||
          order == "multi" ||
          order == "plus" ||
          order == "netflix"
        )
      ) {
        this.Places[this.CurrentPlace].items.splice(key, 1);
        let total = 0;
        this.Places[this.CurrentPlace].items.forEach((item) => {
          total += item.Quantity * item.Price;
        });
        this.Places[this.CurrentPlace].Total = total.toFixed(2);
        await this.UpdateItems();
        await this.GetData();
      } else {
        alert("  ");
      }
    },
    async UpdateItems() {
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            JSON: this.Places,
            JSONW: this.PSW,
            JSONT: this.PST,
          }
        );
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
    },
    async PlayPS(index) {
      if (this.PSWT == "") {
        alert("  ");
      } else {
        this.getTime();
        try {
          const response = await axios.put(
            "http://localhost/cafe-ps8/api/orders.php",
            {
              IdPS: index + 1,
              Time: this.time,
            }
          );
          console.log(response.data.msg);
        } catch (error) {
          console.error("fail:", error);
        }
        if (this.PSTT == 0 || this.PSTT == "") {
          this.PST[index] = 0;
        } else {
          this.PST[index] = Math.abs(Number(this.PSTT));
        }
        this.PSW[index] = this.PSWT;
        this.PSWT = "";
        this.PSTT = 0;
        await this.UpdateItems();
        this.GetData();
      }
    },
    async StopPS(index) {
      this.getTime();
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            IdPS: index + 1,
            stop: 0,
          }
        );
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
      let price;
      if (this.PSW[index] == "normal") {
        price = this.PSs[index].normal_price;
      } else if (this.PSW[index] == "multi") {
        price = this.PSs[index].multi_price;
      } else if (this.PSW[index] == "plus") {
        price = this.PSs[index].plus_price;
      } else if (this.PSW[index] == "netflix") {
        price = this.PSs[index].netflix_price;
      }
      const Start = new Date(this.PSs[index].start.replace(" ", "T"));
      const End = new Date(this.time.replace(" ", "T"));
      const hours = ((End - Start) / (1000 * 60) / 60).toFixed(2);
      this.PST[index] = hours;

      this.Places[index + 8].items.unshift({
        Order: this.PSW[index],
        Quantity: this.PST[index],
        Price: price,
      });
      let total = 0;
      this.Places[index + 8].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      this.Places[index + 8].Total = total.toFixed(2);
      this.PSW[index] = "";
      this.PST[index] = 0;
      await this.UpdateItems();
      this.GetData();
    },
    startInterval() {
      setInterval(() => {
        this.getTime();
      }, 30000);
    },
    async ChangePlaces() {
      const place = this.Places[this.CurrentPlace].items;
      this.Places[this.CurrentPlace].items = this.Places[this.NewPlace].items;
      this.Places[this.NewPlace].items = place;
      const Total = this.Places[this.CurrentPlace].Total;
      this.Places[this.CurrentPlace].Total = this.Places[this.NewPlace].Total;
      this.Places[this.NewPlace].Total = Total;
      await this.UpdateItems();
      await this.GetData();
      this.NewPlace = "";
    },
    async Print(index) {
      if (this.Places[this.CurrentPlace].Total != 0) {
        const printContents = this.$refs[index].outerHTML;
        const originalContents = this.$refs.room.innerHTML;
        this.$refs.room.innerHTML = printContents;
        await this.$nextTick();
        window.print();
        await this.checkdb();
        this.$refs.room.innerHTML = originalContents;
        await window.location.reload();
      } else {
        alert(" ");
      }
    },
    async Print2(index) {
      if (this.Places[this.CurrentPlace].Total != 0) {
        const printContents = this.$refs[index].outerHTML;
        const originalContents = this.$refs.room.innerHTML;
        this.$refs.room.innerHTML = printContents;
        await this.$nextTick();
        window.print();
        this.$refs.room.innerHTML = originalContents;
        await window.location.reload();
      } else {
        alert(" ");
      }
    },
    async checkdb() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            Id: this.id,
            Time: this.time,
            Orders: this.Places[this.CurrentPlace].items,
            Place: this.CurrentPlace,
          }
        );
        console.log(response.data);
        if (response.data.message == " ") {
          this.Places[this.CurrentPlace].items = [];
          this.Places[this.CurrentPlace].Total = 0;
        }
      } catch (error) {
        console.error("Error deleting user:", error.message);
      }
      await this.UpdateItems();
      await this.GetData();
    },
    getTime() {
      const now = new Date();

      // Get date components
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, "0"); // Month is zero-based
      const day = String(now.getDate()).padStart(2, "0");

      // Get time components
      const hours = String(now.getHours()).padStart(2, "0");
      const minutes = String(now.getMinutes()).padStart(2, "0");
      const seconds = String(now.getSeconds()).padStart(2, "0");

      // Create formatted datetime string
      this.time = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    },
  },
};
</script>
