<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      // Show Page
      SalessShow: "",
      id: "",
      username: "",
      // SelectUser
      Users: [],
      SelectedUser: "",
      SelectedDate: "",
      // Data
      Harmonies: [],
      HarmoniesShow: 0,
      MaterialsShow: 0,
      Expenses: [],
      Store: {},
      Purchases: {},
      Sales: {},
      Orders: [],
      Places: [
        " 1",
        " 2",
        " 3",
        " 4",
        " 5",
        " 6",
        " 7",
        " 8",
        "Room 1",
        "Room 2",
        "Room 3",
        "Room 4",
        "Room 5",
        "Room 6",
        "Room 7",
        "Room 8",
        "Delivery",
        "TakeAway",
      ],
      Materials: [],
      FilteredMaterials: [],
      SelectedMaterial: "",
      SalesM: [],
      Rooms: [],
      SalesDiff: [],
      SalesRooms: [],
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.username = this.getCookie("user_name");
    this.SalesShow = this.getCookie("user_sales");
    if (!this.getCookie("user_id")) {
      this.$router.push("/cafe-ps8/");
    }
    this.GetUsers();
  },
  mounted() {
    this.$store.dispatch("globalFunction");
  },
  computed: {
    groupedSalesDiff() {
      return this.SalesDiff.reduce((acc, product) => {
        if (!acc[product.sector]) {
          acc[product.sector] = [];
        }
        acc[product.sector].push(product);

        return acc;
      }, {});
    },
    groupedProducts() {
      return this.Sales.reduce((acc, product) => {
        if (
          product.sector !== "normal" &&
          product.sector !== "multi" &&
          product.sector !== "plus" &&
          product.sector !== "netflix"
        ) {
          if (!acc[product.sector]) {
            acc[product.sector] = [];
          }
          acc[product.sector].push(product);
        }
        return acc;
      }, {});
    },
    groupedPSs() {
      return this.Sales.reduce((acc, product) => {
        // Ensure the accumulator object is always returned
        if (
          product.sector === "normal" ||
          product.sector === "multi" ||
          product.sector === "plus" ||
          product.sector === "netflix"
        ) {
          if (!acc[product.sector]) {
            acc[product.sector] = [];
          }
          acc[product.sector].push(product);
        }
        return acc; // Always return the accumulator
      }, {}); // Initialize the accumulator as an empty object
    },
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async GetUsers() {
      try {
        const response = await axios.get(
          "http://localhost/cafe-ps8/api/Sales.php"
        );
        this.Users = response.data.Users;
        this.Materials = response.data.Materials;
      } catch (error) {
        console.error("fail:", error);
      }
    },
    async SelectUser() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/Sales.php",
          {
            Type: "DataPerDay",
            SelectedUser: this.SelectedUser,
            SelectedDate: this.SelectedDate,
          }
        );
        this.Harmonies = response.data.Harmonies;
        this.Expenses = response.data.Expenses;
        this.Store = response.data.Store;
        this.Purchases = response.data.Purchases;
        this.Sales = response.data.Sales;
        this.Orders = response.data.Orders;
        this.Rooms = response.data.Rooms;
        this.SalesDiff = response.data.SalesDiff;
        this.SalesRoomsM();
        this.HarmoniesShow = 1;
        this.SelectedUser = "";
        this.SelectedDate = "";
      } catch (error) {
        console.error("fail:", error);
      }
    },
    SalesRoomsM() {
      const groupedRooms = this.Rooms.reduce((acc, Room) => {
        if (Room && Room.place !== undefined) {
          if (!acc[Room.place]) {
            acc[Room.place] = [];
          }
          acc[Room.place].push(Room);
        }
        return acc;
      }, {});

      this.SalesRooms = groupedRooms;
    },
    async SelectMaterial() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/Sales.php",
          {
            Type: "Materials",
            SelectedMaterial: this.SelectedMaterial,
          }
        );
        this.FilteredMaterials = this.Materials.filter(
          (item) => item.id == this.SelectedMaterial
        )[0];
        this.SalesM = response.data.SalesM;
        this.MaterialsShow = 1;
        this.SelectedMaterial = "";
      } catch (error) {
        console.error("fail:", error);
      }
    },
    CalcTotal(items) {
      return items.reduce((total, item) => {
        return (
          Number(total) +
          Number(item.price) * Number(item.quantity)
        ).toFixed(2);
      }, 0);
    },
    CalcSum(items) {
      return items.reduce((total, item) => {
        return (Number(total) + Number(item.sum)).toFixed(2);
      }, 0);
    },
    CalcSum2(items) {
      return items.reduce((total, sale) => {
        return (Number(total) + Number(sale.num) * Number(sale.count)).toFixed(
          2
        );
      }, 0);
    },
    CalcSum3(items) {
      return items.reduce((total, sale) => {
        return (Number(total) + Number(sale.sum)).toFixed(2);
      }, 0);
    },
    CalcSum4(items) {
      return items.reduce((total, sale) => {
        return (Number(total) + Number(sale.price)).toFixed(2);
      }, 0);
    },
    async Print(index) {
      const printContents = this.$refs["printsection" + index][0].outerHTML;
      const originalContents = this.$refs.sales.innerHTML;
      this.$refs.sales.innerHTML = printContents;
      await this.$nextTick();
      window.print();
      this.$refs.sales.innerHTML = originalContents;
      await window.location.reload();
    },
    async PrintAll() {
      const printContents = this.$refs.All.outerHTML;
      const originalContents = this.$refs.sales.innerHTML;
      this.$refs.sales.innerHTML = printContents;
      await this.$nextTick();
      window.print();
      this.$refs.sales.innerHTML = originalContents;
      await window.location.reload();
    },
  },
};
</script>
