// store/index.js
import { createStore } from "vuex";
import axios from "axios";

const apiBaseUrl = "http://localhost/cafe-ps8/api/orders.php";

export default createStore({
  state: {
    // Show Page
    RoomsShow: "",
    id: "",
    username: "",
    // Rooms
    Places: [
      { id: "Room1", Room: " 1", items: [], Total: 0 },
      { id: "Room2", Room: " 2", items: [], Total: 0 },
      { id: "Room3", Room: " 3", items: [], Total: 0 },
      { id: "Room4", Room: " 4", items: [], Total: 0 },
      { id: "Room5", Room: " 5", items: [], Total: 0 },
      { id: "Room6", Room: " 6", items: [], Total: 0 },
      { id: "Room7", Room: " 7", items: [], Total: 0 },
      { id: "Room8", Room: " 8", items: [], Total: 0 },
      { id: "PS1", Room: "PS1", items: [], Total: 0 },
      { id: "PS2", Room: "PS2", items: [], Total: 0 },
      { id: "PS3", Room: "PS3", items: [], Total: 0 },
      { id: "PS4", Room: "PS4", items: [], Total: 0 },
      { id: "PS5", Room: "PS5", items: [], Total: 0 },
      { id: "PS6", Room: "PS6", items: [], Total: 0 },
      { id: "PS7", Room: "PS7", items: [], Total: 0 },
      { id: "PS8", Room: "PS8", items: [], Total: 0 },
      { id: "Delivery", Room: "Delivery", items: [], Total: 0 },
      { id: "TakeAway", Room: "TakeAway", items: [], Total: 0 },
    ],
    Place: {},
    CurrentPlace: "",
    NewPlace: "",
    ButtonsShow: 1,
    Sectors: [],
    Products: [],
    FilteredProducts: [],
    Sector: "",
    Product: "",
    Quantity: "",
    Product2: "",
    Quantity2: "",
    //PSs
    PSs: [],
    PSWT: "",
    PSTT: 0,
    PSW: ["", "", "", "", "", "", "", ""],
    PST: [0, 0, 0, 0, 0, 0, 0, 0],
    // time
    time: "",
  },
  mutations: {
    SET_PLACES(state, places) {
      state.Places = places;
    },
    SET_SECTORS(state, sectors) {
      state.Sectors = sectors;
    },
    SET_PRODUCTS(state, products) {
      state.Products = products;
    },
    SET_PS(state, ps) {
      state.PSs = ps;
    },
    SET_PSW(state, psw) {
      state.PSW = psw;
    },
    SET_PST(state, pst) {
      state.PST = pst;
    },
    SET_TIME(state, time) {
      state.time = time;
    },
  },
  actions: {
    async globalFunction({ dispatch }) {
      setInterval(async () => {
        await dispatch("GetData");
        await dispatch("getTime");
        for (let i = 0; i < 8; i++) {
          if (this.state.PST[i] != 0 && this.state.PSs[i].state == 1) {
            const Start = new Date(this.state.PSs[i].start.replace(" ", "T"));
            const End = new Date(this.state.time.replace(" ", "T"));
            const hours = ((End - Start) / (1000 * 60 * 60)).toFixed(2);
            console.log(hours);
            if (this.state.PST[i] - hours <= 0) {
              await dispatch("StopPS", i);
            }
          }
        }
      }, 30000);
    },
    getTime({ commit }) {
      const now = new Date();

      // Get date components
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, "0"); // Month is zero-based
      const day = String(now.getDate()).padStart(2, "0");

      // Get time components
      const hours = String(now.getHours()).padStart(2, "0");
      const minutes = String(now.getMinutes()).padStart(2, "0");
      const seconds = String(now.getSeconds()).padStart(2, "0");

      // Create formatted datetime string
      const time = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
      commit("SET_TIME", time);
    },

    async UpdateItems({ state }) {
      try {
        const response = await axios.put(apiBaseUrl, {
          JSON: state.Places,
          JSONW: state.PSW,
          JSONT: state.PST,
        });
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
    },
    async GetData({ commit, state }) {
      try {
        const response = await axios.get(apiBaseUrl, {
          params: {
            timestamp: new Date().getTime(),
          },
        });
        commit("SET_PLACES", JSON.parse(response.data.Places[0].orders));
        commit("SET_PSW", JSON.parse(response.data.Places[1].orders));
        commit("SET_PST", JSON.parse(response.data.Places[2].orders));
        commit("SET_PS", response.data.PSs);
        commit("SET_PRODUCTS", response.data.Products);
        commit("SET_SECTORS", response.data.Sectors);
      } catch (error) {
        console.error("fail:", error);
      }
      state.Place = state.Places[state.CurrentPlace];
    },
    async StopPS({ dispatch, commit, state }, index) {
      await dispatch("getTime");
      try {
        const response = await axios.put(apiBaseUrl, {
          IdPS: index + 1,
          stop: 0,
        });
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
      let price;
      if (state.PSW[index] == "normal") {
        price = state.PSs[index].normal_price;
      } else if (state.PSW[index] == "multi") {
        price = state.PSs[index].multi_price;
      } else if (state.PSW[index] == "plus") {
        price = state.PSs[index].plus_price;
      } else if (state.PSW[index] == "netflix") {
        price = state.PSs[index].netflix_price;
      }
      const Start = new Date(state.PSs[index].start.replace(" ", "T"));
      const End = new Date(state.time.replace(" ", "T"));
      const hours = ((End - Start) / (1000 * 60) / 60).toFixed(2);
      commit("SET_PST", [
        ...state.PST.slice(0, index),
        hours,
        ...state.PST.slice(index + 1),
      ]);

      state.Places[index + 8].items.unshift({
        Order: state.PSW[index],
        Quantity: hours,
        Price: price,
      });
      let total = 0;
      state.Places[index + 8].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      commit("SET_PLACES", [
        ...state.Places.slice(0, index + 8),
        { ...state.Places[index + 8], Total: total.toFixed(2) },
        ...state.Places.slice(index + 9),
      ]);
      commit("SET_PSW", [
        ...state.PSW.slice(0, index),
        "",
        ...state.PSW.slice(index + 1),
      ]);
      commit("SET_PST", [
        ...state.PST.slice(0, index),
        0,
        ...state.PST.slice(index + 1),
      ]);
      await dispatch("UpdateItems");
      await dispatch("GetData");
      window.location.reload();
    },
  },
  getters: {
    allPlaces(state) {
      return state.Places;
    },
    allProducts(state) {
      return state.Products;
    },
    allPSs(state) {
      return state.PSs;
    },
    getTime(state) {
      return state.time;
    },
  },
});
