import { createRouter, createWebHistory } from "vue-router";
import Login from "../components/LoginView.vue";
import Home from "../components/HomeView.vue";
import PS from "../components/PSView.vue";
import Affairs from "../components/AffairsView.vue";
import Purchases from "../components/PurchasesView.vue";
import Sales from "../components/SalesView.vue";
import Products from "../components/ProductsView.vue";
import Rooms from "../components/RoomsView.vue";
import Harmony from "../components/HarmonyView.vue";
import Expenses from "../components/ExpensesView.vue";
import Store from "../components/StoreView.vue";

const routes = [
  { path: "/cafe-ps8/", component: Login },
  { path: "/cafe-ps8/home", component: Home },
  {
    path: "/cafe-ps8/affairs",
    component: Affairs,
  },
  {
    path: "/cafe-ps8/ps",
    component: PS,
  },
  { path: "/cafe-ps8/purchases", component: Purchases },
  { path: "/cafe-ps8/sales", component: Sales },
  { path: "/cafe-ps8/products", component: Products },
  { path: "/cafe-ps8/rooms", component: Rooms },
  { path: "/cafe-ps8/harmony", component: Harmony },
  { path: "/cafe-ps8/expenses", component: Expenses },
  { path: "/cafe-ps8/store", component: Store },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
